#home {
    background-color: var(--body-color);
}

.home_container {
    gap: 1rem;
}

.home_content {
    grid-template-columns: 0.5fr 3fr;
    padding-top: 3.5rem;
    align-items: center;
}

.home_social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home_social-icon {
    font-size: 1.25rem;
    color: var(--first-color-alt);
}

.home_social-icon:hover {
    color: var(--first-color);
}

